<template>
<div class="card">
    <h5 class="card-header">Encounters</h5>
    <div class="card-body" ref="acute">
        <EncounterTimeLine v-if="acuteEncountersList.length !==0" 
                           :heading="'Acute'" 
                           :filteredEncountersList="acuteEncountersList" 
                           :cchsCoreConfig="cchsCoreConfig"/>

        <EncounterTimeLine v-if="primaryCareEncountersList.length !==0" 
                           :heading="'Primary Care'" 
                           :filteredEncountersList="primaryCareEncountersList" 
                           :cchsCoreConfig="cchsCoreConfig"/>

        <EncounterTimeLine v-if="specialityEncountersList.length !==0" 
                           :heading="'Speciality'" 
                           :filteredEncountersList="specialityEncountersList" 
                           :cchsCoreConfig="cchsCoreConfig"/>
        
    </div>
</div>
</template>

<script>
import Utils from "../../cchs-utils";
import EncounterTimeLine from "./EncounterTimeLine";
export default {
    name:'Encounters',
    components:{EncounterTimeLine},
    props:['encountersList', 'cchsCoreConfig'],
    data(){
        return{
            width: null,
            offSet: 300,
            acuteEncountersList: [],
            primaryCareEncountersList: [],
            specialityEncountersList: []
        }
    },
    methods:{
        calculateWidth() {
            this.width = this.$refs.acute.clientWidth - this.offSet;
        },
        groupEncounters(){
            let x = this.width/this.encountersList.length;
            let counter = x;
            this.acuteEncountersList = [];
            this.specialityEncountersList = [];
            this.primaryCareEncountersList = [];
            this.encountersList.forEach(e => {
                e.x = x;
                e.start = Utils.getDateInReadableMMDDYYYYFormat(e.period.start)
                if(e.location.find(c => c.location.display.indexOf('_HOSP') !== -1 || c.location.display.indexOf('Hospital') !== -1) !== undefined)
                    this.acuteEncountersList.push(e);
                else if(e.location.find(c => c.location.display.indexOf('Primary Care') !== -1) !== undefined)
                    this.primaryCareEncountersList.push(e);
                else
                    this.specialityEncountersList.push(e);
                x+=counter;
            });
        },
        resizeRectangle(e){
            console.log('e')
            this.calculateWidth();
            this.groupEncounters();
        }
    },
    computed:{
    },
    mounted(){
        alert("point a");
        //alert(window.external.XMLCclRequest);
        //alert(window.external.XMLCclRequest());
        //alert(this);
        var requestSync = window.external.XMLCclRequest();
        alert(requestSync);
        //requestSync.open("GET", "asd_smart_on_fhir_utilities:dba", false);

        
        requestSync.open("GET", "cchs_mp_get_latest_shared_plan:dba" ,0);
        alert("point b");
        requestSync.send('"MINE", 17379680.0, 17461509.0, "GYNPLAN"');
        alert("point c");
        if (requestSync.status == 200) {
            alert("success");
            alert(requestSync.responseText.replace(/{/g, '\n{'));
            logger.logMessage(requestSync.responseText);
        } else {
            alert("fail");
            alert(requestSync.status);
        }
        requestSync.cleanup();
        alert("point d");
        

        /*
        requestSync.open("GET", "eks_get_win32_components", false);
        requestSync.send('"MINE", 8, 0, "mpObj", "GETCODESET"');
        if (requestSync.status == 200) {
            alert(requestSync.responseText.replace(/{/g, '\n{'));
        }
        else {
            alert('XMLCclRequest failed with status of ' + requestSync.status);
        }
        requestSync.cleanup();
        */
        this.calculateWidth();
        this.groupEncounters();
        window.addEventListener('resize', this.resizeRectangle);
    },
    beforeDestroy(){
        window.removeEventListener('resize', this.resizeRectangle)
    }
}
</script>

