import moment from 'moment'

function processResponseData(response){
    let processedResponse = [];
    if(response!==undefined){
        response.forEach(function(e) {
            processedResponse.push(e.resource);
          });
    }    
    return processedResponse;
}
function fetchAllRecords(axiosObject, url, inputParams, output, resolve, reject){
    axiosObject.get(url, {params: inputParams !== undefined ? inputParams : ''})
    .then(response => {
        const retrivedRecords =  output.concat(processResponseData(response.data.entry));
        let nextPageURI = response.data.link.find(e=> e.relation==='next')
        if (nextPageURI !== undefined) {
            fetchAllRecords(axiosObject, nextPageURI.url, undefined, retrivedRecords, resolve, reject)
          } else {
            resolve(retrivedRecords)
          }
    })
    .catch(error_response => {
        console.log(error_response)
        reject('Something wrong. Please refresh the page and try again.')
    });
}
function getDateInReadableMMDDYYYYFormat(date){
    if(date !== undefined)
        return moment(date).format('MM/DD/YYYY');
    else
        return 'NA'
}

function getDateInReadableFormat(dateString, formatPattern){
    if(dateString !== undefined)
        return moment(dateString).format(formatPattern);
    else
        return ''
}
export default {
    processResponseData,
    fetchAllRecords,
    getDateInReadableMMDDYYYYFormat,
    getDateInReadableFormat
}